import style from "./Logo.module.scss";

export const Logo = () => {
  return (
    <svg
      className={style.logo}
      height="200"
      viewBox="0 0 600 200"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
    >
      <rect width="600" height="200" fill="url(#pattern0)" />
      <defs>
        <pattern
          id="pattern0"
          patternContentUnits="objectBoundingBox"
          width="1"
          height="1"
        >
          <use
            xlinkHref="#image0_1_2"
            transform="matrix(0.000789889 0 0 0.00236967 -0.00592417 0)"
          />
        </pattern>
        <image
          id="image0_1_2"
          width="1281"
          height="422"
          xlinkHref="data:image/png;base64,"
        />
      </defs>
    </svg>
  );
};
